import { mapMutations, mapState, mapActions } from 'vuex';
export default {
	data() {
		return {
			token: null,
			type: 0,
			tabs: [
				{
					name: '通知',
					url: 'Message/getDataList',
					list: [],
					pageNum: 1,
					pageSize: 20,
					loading: true,
					more: false
				},
				{
					name: '消息',
					url: 'user_chat/getDataList',
					list: [],
					pageNum: 1,
					pageSize: 20,
					loading: true,
					more: false
				}
			]
		};
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	computed: { ...mapState(['uList', 'msgNum']) },
	onLoad(opt) {
		const token = uni.getStorageSync('TOKEN');
		if (token) this.getList();
		if (opt.user) {
			console.log(opt.user);
		}
	},
	onShow() {
		console.log(this.uList[0]);
		this.token = uni.getStorageSync('TOKEN');
	},
	onReachBottom() {
		this.getList();
	},
	onPullDownRefresh() {
		this.tabs = [
			{
				name: '通知',
				url: 'Message/getDataList',
				list: [],
				pageNum: 1,
				pageSize: 20,
				loading: true,
				more: false
			},
			{
				name: '消息',
				url: 'user_chat/getDataList',
				list: [],
				pageNum: 1,
				pageSize: 20,
				loading: true,
				more: false
			}
		];
		this.getList();
	},
	methods: {
		...mapMutations(['updateUlist', 'setMsgNum']),
		...mapActions(['navFilter']),
		jump(item) {
			if (!item.id) {
				item.id = item.user_id;
			}
			// console.log(item.count)
			// this.setMsgNum(this.msgNum -item.count);
			// item.count=0;
			// this.updateUlist(this.uList);
			uni.navigateTo({
				url: 'chat/chat?user=' + JSON.stringify(item)
			});
		},
		doClick(item) {
			if (item.type === 0) {
				uni.navigateTo({
					url: 'detail/detail?id=' + item.id
				});
				item.is_read = 1;
			}
		},
		del(id, k) {
			this.$Fetch({
				url: 'Message/del_message',
				data: { id: id },
				success: res => {
					let tabs = this.tabs;
					tabs[this.type].list.splice(k, 1);
					this.tabs = tabs;
				}
			});
		},
		onChange(e) {
			this.type = e.detail.index;
			const token = uni.getStorageSync('TOKEN');
			if (token) {
				if (this.tabs[e.detail.index].list.length === 0) {
					this.tabs[e.detail.index].loading = true;
					this.getList();
				}
			}
		},
		getList() {
			let tabs = this.tabs;
			let type = this.type;
			if (tabs[type].loading) {
				tabs[type].loading = false;
				this.tabs = tabs;
				this.$Fetch({
					url: tabs[type].url,
					data: {
						pageNum: tabs[type].pageNum,
						pageSize: tabs[type].pageSize
					},
					success: res => {
						let len = res.length;
						if (len > 0) {
							tabs[type].list = tabs[type].list.concat(res);
							if (len === tabs[type].pageSize) {
								tabs[type].pageNum++;
								tabs[type].loading = true;
								tabs[type].more = true;
							} else {
								tabs[type].more = false;
							}
						} else {
							tabs[type].more = false;
						}
						this.tabs = tabs;
					}
				});
			}
		}
	}
};
